import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/Layout/Layout';
import Products from '../components/Products/Products';
export const query = graphql`
	{
		allContentfulProduct {
			edges {
				node {
					title
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
				}
			}
		}
	}
`;
function ProductsPage({ data }) {
	return (
		<Layout title="محصولات">
			<p className="text-center mt-5 mb-0 pr-5 pl-5" style={{ fontWeight: 'bold', color: 'blue' }} dir="rtl">
				جهت اطلاع از قیمت و موجودی محصولات با شماره ۰۹۰۴۶۵۵۸۲۸۳ تماس بگیرید
			</p>
			<Products products={data.allContentfulProduct.edges} />
		</Layout>
	);
}

export default ProductsPage;
